"use client";
import { useDispatch } from "react-redux";
import { useRouter } from "next/navigation";
import { setSearchedQuery } from "@/redux/slices/jobSlice";
import { ChevronLeft, ChevronRight } from "lucide-react";
import { useRef } from "react";

const CategoryCarousel = () => {
  const dispatch = useDispatch();
  const router = useRouter();
  const scrollRef = useRef(null);

  const categories = [
    "Frontend Developer",
    "Backend Developer",
    "Data Science",
    "Graphic Designer",
    "FullStack Developer",
    "Mobile Developer",
    "Cybersecurity",
    "Product Manager",
    "DevOps Engineer",
  ];

  const searchJobHandler = (query) => {
    dispatch(setSearchedQuery(query));
    router.push(`/browse`);
  };

  const scrollLeft = () => {
    if (scrollRef.current) {
      scrollRef.current.scrollBy({ left: -200, behavior: "smooth" });
    }
  };

  const scrollRight = () => {
    if (scrollRef.current) {
      scrollRef.current.scrollBy({ left: 200, behavior: "smooth" });
    }
  };

  return (
    <section className="my-16 px-4 relative">
      <h2 className="text-center text-2xl sm:text-3xl font-semibold mb-6">
        Explore Categories
      </h2>

      {/* Left Button */}
      <button
        className="absolute left-0 top-1/2 -translate-y-1/2 p-2 bg-white shadow-md rounded-full z-10 hidden sm:flex"
        onClick={scrollLeft}
      >
        <ChevronLeft className="w-6 h-6 text-gray-600" />
      </button>

      <div
        ref={scrollRef}
        className="flex overflow-x-auto no-scrollbar gap-4 p-2 scroll-smooth"
      >
        {categories.map((cat, index) => (
          <button
            key={index}
            onClick={() => searchJobHandler(cat)}
            className="whitespace-nowrap px-4 py-2 text-sm sm:text-base rounded-full border border-gray-300 transition-all hover:bg-gray-200"
          >
            {cat}
          </button>
        ))}
      </div>

      {/* Right Button */}
      <button
        className="absolute right-0 top-1/2 -translate-y-1/2 p-2 bg-white shadow-md rounded-full z-10 hidden sm:flex"
        onClick={scrollRight}
      >
        <ChevronRight className="w-6 h-6 text-gray-600" />
      </button>
    </section>
  );
};

export default CategoryCarousel;

